<template>
  <div class="side-navigation">
    <!-- <v-btn fab class="btn-side-drawer" @click.stop="drawer = !drawer" v-show="$vuetify.breakpoint.smAndDown"
      ><v-icon>mdi-menu</v-icon></v-btn
    > -->
    <v-navigation-drawer
      v-model="drawer"
      :mini-variant.sync="mini"
      :class="barcolor"
      app
      :disable-resize-watcher="false"
    >
      <v-list-item class="px-5 pt-3">
        <v-list-item-avatar>
          <v-img src="../../assets/profile.jpg"></v-img>
        </v-list-item-avatar>

        <v-list-item-title>{{ name }}</v-list-item-title>
      </v-list-item>

      <!-- <v-list-item class="pl-16">
        <v-list-item-title class="caption">{{ emailAddress }}</v-list-item-title>
      </v-list-item> -->

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          @click="scrollTo(item.path)"
          :class="textcolor"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <DownloadResume color="white" width="90%" />
      </template>
    </v-navigation-drawer>
  </div>
</template>

<script>
import axios from "axios";
import DownloadResume from "./DownloadResume.vue";

export default {
  components: {
    DownloadResume,
  },
  data() {
    return {
      name: "Lucas Lin",
      emailAddress: "linhtetaung0102@gmail.com",
      drawer: null,
      barcolor: "",
      textcolor: "text-decoration-none",
      enableBlog: false,
      items: [
        {
          title: "Home",
          path: "#aboutme-section",
          icon: "mdi-home",
        },
        {
          title: "Skills",
          path: "#skills-section",
          icon: "mdi-star",
        },
        {
          title: "Projects",
          path: "#projects-section",
          icon: "mdi-code-braces-box",
        },
        {
          title: "Experience",
          path: "#timeline-section",
          icon: "mdi-timeline",
        },
      ],
      mini: false,
    };
  },
  methods: {
    scrollTo(section) {
      try {
        this.$vuetify.goTo(section);
      } catch {
        this.$router.push("/"+section);
      }
    },
  },
  async created() {
    try {
      if(this.enableBlog == true){
        const response = await axios.get(
          `${process.env.VUE_APP_SERVICE_URL}/blogs/count`
        );
        if(response.data > 0){
        this.items.push(
          {
            title: "Blogs",
            path: "#blogs-section",
            icon: "mdi-card-text",
          }
        )
        }
      }
    } catch (error) {
      this.error = error;
    }
  },
};
</script>