<template>
  <div class="container">
    <div class="wrapper">
      <section id="toggle-me">
        <div class="toggle-wrapper">
          <div class="toggle normal">
            <input type="checkbox" id="chkToggle" class="check" />
            <label class="toggle-item" for="chkToggle"></label>
          </div>
        </div>
        <label class="toggle-text" for="chkToggle">Toggle to un-mess!</label>
      </section>
      <div class="scattered-canvas"></div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "satisfy",
  mounted: function () {
    function rand(min, max) {
      return Math.floor(Math.random() * (max + 1)) + min;
    }

    function Scatter() {
      $(".parts").removeClass("organized");
      $(".scattered-canvas").removeClass("organized");
      $(".confetti").remove();

      const parts = 20,
        confetti = $('<div class="confetti"></div>');

      $(".scattered-canvas").append(confetti);

      let i = 0;
      for (i = 0; i < parts; i++) {
        const x =
            confetti.width() / 2 +
            rand(70, 100) *
              Math.cos((2 * Math.PI * i) / rand(parts - 10, parts + 10)),
          y =
            confetti.height() / 4 +
            rand(10, 140) *
              Math.sin((1 * Math.PI * i) / rand(parts - 10, parts + 10)),
          color = rand(0, 255) + ", " + rand(0, 255) + ", " + rand(0, 255),
          elm =
            '<div class="parts" style="' +
            "background-color: rgb(" +
            color +
            ") ;" +
            "top: " +
            y +
            "px; " +
            "left: " +
            x +
            'px;"></div>';
        confetti.append(elm);
      }
    }

    $(document).on("change", "#chkToggle", function () {
      if (this.checked) {
        // start the organizing
        let top = 20,
          left = 20;
        const offset = 20;

        $(".parts").each(function (index) {
          $(this).animate({ top: top + "px", left: left + "px" }, "slow");
          // small screen
          if($('.confetti').width()===250)
          {
            left += 20 + offset;
            if ((index + 1) % 5 === 0) {
            top += 20 + offset;
            left = 20;
          }
          }
          // big screen
          else{
            left += 50 + offset;
            if ((index + 1) % 5 === 0) {
              top += 50 + offset;
              left = 20;
            }
          }
        });
      } else {
        Scatter();
      }
    });

    // start the scattering
    Scatter();
  },
};
</script>

<style lang="scss" scoped>
::-webkit-scrollbar {
  display: none;
}

$background: #ebf5fc;
$fontcolor: #000;
$text-color: #555959;
$switch-bg: #ebf5fc;
$text-color-focus: #0047ab;

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto", sans-serif;
  color: $fontcolor;
}

@media (min-width: 1265px) {
  .scattered-canvas {
    min-width: 400px !important;
    height: 500px !important;
  }
}

@media (min-width: 768px) {
  .scattered-canvas {
    width: 250px;
    height: 350px;
  }
}

@media (max-width: 768px) {
  .scattered-canvas {
    width: 250px;
    height: 350px;
  }
}

#toggle-me * {
  box-sizing: border-box;
  &:before,
  &:after {
    content: "";
    position: absolute;
  }
}

.toggle-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.toggle {
  position: relative;
  display: inline-block;
}

input.check {
  height: 40px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 40px;
}

label.toggle-item {
  width: 7em;
  background: $background;
  height: 2.2em;
  display: inline-block;
  border-radius: 50px;
  margin: 10px;
  position: relative;
  transition: all 0.3s ease;
  transform-origin: 20% center;
  cursor: pointer;
  &:before {
    display: block;
    transition: all 0.2s ease;
    width: 2.3em;
    height: 2.3em;
    top: 0.25em;
    left: 0.25em;
    border-radius: 2em;
    border: 1px solid $background;
    transition: 0.3s ease;
  }
}

.normal {
  .toggle-item {
    background: #ddd;
    border: 1px solid #ddd;
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
      3px 3px 5px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
      3px 3px 5px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
      3px 3px 5px rgba(0, 0, 0, 0.1);
    &:before {
      border: none;
      width: 1.6em;
      height: 1.6em;
      box-shadow: inset 0.5px -1px 1px rgba(0, 0, 0, 0.35);
      background: $text-color;
      transform: rotate(-25deg);
    }
    &:after {
      background: transparent;
      height: calc(100% + 8px);
      border-radius: 30px;
      top: -5px;
      width: calc(100% + 8px);
      left: -4px;
      z-index: 0;
    }
  }
}

#chkToggle:checked + label {
  background: $switch-bg;
  box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
    3px 3px 5px rgba(0, 0, 0, 0.1) inset;
  -webkit-box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
    3px 3px 5px rgba(0, 0, 0, 0.1) inset;
  -moz-box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
    3px 3px 5px rgba(0, 0, 0, 0.1) inset;
  &:before {
    left: 77px;
    background: $text-color-focus;
  }
}
</style>

<style lang="scss">
@media (min-width: 1265px) {
  .confetti {
    position: absolute;
    width: 400px !important;
    height: 500px !important;
    pointer-events: none;
  }

  .parts {
    position: absolute;
    min-width: 50px !important;
    height: 50px !important;
    margin: 20px;
    animation: pops 01.3s forwards;
  }
}

@media (min-width: 768px) {
  .confetti {
    position: absolute;
    width: 250px;
    height: 350px;
    pointer-events: none;
  }

  .parts {
    position: absolute;
    min-width: 20px;
    height: 20px;
    margin: 20px;
    animation: pops 1.3s forwards;
  }
}

@media (max-width: 768px) {
  .confetti {
    position: absolute;
    width: 250px;
    height: 350px;
    pointer-events: none;
  }

  .parts {
    position: absolute;
    min-width: 20px;
    height: 20px;
    margin: 20px;
    animation: pops 1.3s forwards;
  }
}

@media (max-width: 600px) {
  .confetti {
    position: absolute;
    width: 250px;
    height: 350px;
    pointer-events: none;
  }

  .parts {
    position: absolute;
    min-width: 20px;
    height: 20px;
    margin: 10px!important;
    animation: pops 1.3s forwards;
  }
}

@keyframes pops {
  from {
    top: 50%;
    left: 50%;
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>