<template>
    <div class='container vanish'>
        <div id='btn-delete'><i class='fa fa-trash'></i></div>
    </div>
</template>

<script>
import $ from "jquery";

export default {
    name: 'vanish',
    mounted: function(){
        
        // get random number between min and max value
        function rand(min, max) {
        return Math.floor(Math.random() * (max + 1)) + min;
        }
        
        // display trash button again after animation
        function displayTrashButton(){
            $('.explosion').remove();
            $('#btn-delete').prop('style','display:flex;');
        }

        // explosion construction
        function explode(x, y) {
        const particles = 25,
            // explosion container and its reference to be able to delete it on animation end
            explosion = $('<div class="explosion"></div>');

        // put the explosion container into the body to be able to get it's size
        $('.container.vanish').append(explosion);

        let i = 0;
        for (i = 0; i < particles; i++) {
            // positioning x,y of the particle on the circle (little randomized radius)
            const x = (explosion.width() / 2) + rand(80, 150) * Math.cos(2 * Math.PI * i / rand(particles - 10, particles + 10)),
            y = (explosion.height() / 2) + rand(80, 150) * Math.sin(2 * Math.PI * i / rand(particles - 10, particles + 10)),
            color = rand(0, 255) + ', ' + rand(0, 255) + ', ' + rand(0, 255), // randomize the color rgb
                // particle element creation (could be anything other than div),
            size = rand(0, 40),
            elm = '<div class="particle" style="' +
                'background-color: rgb(' + color + ') ;' +
                'top: ' + y + 'px; ' +
                'left: ' + x + 'px;'+
                'width: ' + size + 'px; ' +
                'height: ' + size + 'px;"></div>';
             $('.explosion').append(elm);

             setTimeout(displayTrashButton, 2000);
        }
        }

        // click event listener
        $('#btn-delete').on('click', function(e) {
            // vanish the button
            $('#btn-delete').prop('style','display:none;');
            // start the explosion
            explode(e.pageX, e.pageY);
        })
    }
}
</script>

<style lang="scss" scoped>
::-webkit-scrollbar {
 display: none;
}

// Variables
$super-red: #ed0511;
$sad-red: #e66067;
$white: #ffffff;

.container {
  display:flex;
  justify-content:center;
  align-items:center;
  width:90%;
  overflow: hidden;
}


#btn-delete {
    display:flex;
    justify-content:center;
    align-items:center;
    background-color: $sad-red;
    border-radius: 50%;
    color: $white;
    font-size: 100px;
    height: 200px;
    width: 200px;
  &:hover {
        animation:button 0.3s forwards;
        transition:background-color 0.1s 0.3s, color 0.1s 0.3s;
        color: $white;
        background-color: $super-red;
      }
  
  .fa:hover{
    animation: button 2s forwards infinite;
  }
}


@media (max-width: 768px) {
  #btn-delete {
    font-size: 50px;
    height: 100px;
    width: 100px;
  }
}

@keyframes button {
  60% {
    transform:scale3d(0.8, 0.8, 1);
  }
  85% {
    transform:scale3d(1.1, 1.1, 1);
  }
  100% {
    transform:scale3d(1, 1, 1);
  }
}

</style>

<style lang="scss">
// explosion container
.explosion {
  position: absolute; // required if positioned on click else 'relative'
  width: 600px;
  height: 600px;
  pointer-events: none; // make it clickable trhough
  
  // particle styling
  .particle {
    position: absolute; // required  
    width: 10px;
    height: 10px;
    // border-radius: 50%;
    animation: pop 0.8s reverse forwards; // required
  }
}


// animation for particle fly away from the cursor
@keyframes pop {
  from {
    opacity: 0;
  }
  to {
    top: 50%;
    left: 50%;
    opacity: 1;
  }
}
</style>
