<template>
    <div class='container'>
    <div class='glass-card'>
        <div class='tab-action-bar'>
            <div class='tab-action'>
            <div class='tab-icon'><i class="fas fa-home" data-active="fa-home" data-inactive="fa-home"></i><label>Home</label></div><div class='tab-active'></div>
            </div>
            <div class='tab-action'>
            <div class='tab-icon'><i class="fas fa-bell" data-active="fa-bell" data-inactive="fa-bell"></i><label>Notification</label></div><div class='tab-active'></div>
            </div>
            <div class='tab-action'>
            <div class='tab-icon'><i class="fas fa-user" data-active="fa-user" data-inactive="fa-user"></i><label>Profile</label></div><div class='tab-active'></div>
            </div>
            <div class='tab-action'>
            <div class='tab-icon'><i class="fas fa-comment-alt" data-active="fa-comment-alt" data-inactive="fa-comment-alt"></i><label>Message</label></div><div class='tab-active'></div>
            </div>
        </div>
        <div class='section-content'>
            loading ...
        </div>
    </div>
  </div>
</template>


<script>
import $ from "jquery";

export default {
    name: 'glassy',
    mounted: function(){
        // Default tab action set
        $(document).ready(function() {
        $(".tab-action")[0].click();
        })
        
        function setLoading(text){
            $(".section-content").html(text);
        }

        // Tab action click event
        $(".tab-action").click(function(){
        $('.tab-action').removeClass('active'); 
        
        $(".tab-action>.tab-icon>.fas").each(function(index,item){
        $(item).prop('class','fas '+$(item).data('inactive'));
        });
        
        if($(this).hasClass('active'))
            {
            $(this).removeClass('active');              
            $(this).find('.fas').prop('class','fas '+$(this).find('.fas').data('inactive'));
            }
        else
            {
            $(this).addClass('active'); $(this).find('.fas').prop('class','fas '+$(this).find('.fas').data('active'));
            
            setLoading($(this).find('label').html());
            }
        });

    }
}
</script>

<style lang="scss" scoped>
::-webkit-scrollbar {
 display: none;
}

.container{
  display:flex;
  justify-content:center;
  align-items:center;
  height: 500px;
  width:90%;
  overflow: hidden;
}

.glass-card{
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding:12px;
  border-radius:10px;
  height:250px;
  min-width: 500px;
  background: linear-gradient(156deg, rgba(5,163,160,0.8) 48%, rgba(11,2,191,0.7) 100%);
  box-shadow: 0 0 80px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif;
  z-index: 5;
}

.tab-action-bar{
  height:80px;
  padding:12px;
  display:flex;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.tab-action{
  margin: 0 5px 0 5px;
  border-radius:10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align:center;
  justify-content: center;
  width:100%;
}
.tab-action:hover, .tab-action.active{
  cursor: pointer;
}
.tab-action:hover>.tab-active, .tab-action.active>.tab-active{
  background-color:#e4eff2;
  padding:2px;
  flex-basis: 100%;
  height: 0;
  margin-bottom:-4px;
}
.tab-action:hover>.tab-icon>.fas, .tab-action.active>.tab-icon>.fas{
  color: #e4eff2;
  font-size:30px;
  transform: rotate(20deg);
}
.tab-action:hover>.tab-icon>label,.tab-action.active>.tab-icon>label{
  color: #e4eff2;
  display:block;
  cursor:pointer;
}
.tab-action>.tab-icon>.fas{
  width:50px;
  font-size:25px;
  color: rgba(200, 200, 200, 0.7);
}
.tab-icon>label{
  display:none;
  font-size:12px;
  margin:5px;
}


@media (max-width: 768px) {
  .glass-card{
    min-width:270px;
  }

  .tab-action:hover>.tab-icon>.fas, .tab-action.active>.tab-icon>.fas{
  color: #e4eff2;
  font-size:15px;
  transform: rotate(20deg);
  }
    
  .tab-action>.tab-icon>.fas{
    width:30px;
    font-size:10px;
    color: rgba(200, 200, 200, 0.7);
  }
    
  .tab-icon>label{
    display:none;
    font-size:10px;
    margin:5px;
  }
}

.section-content{
  border-radius:5px;
  height:150px;
  padding:12px;
  margin-top:10px;
  display: flex;
  align-items: center;
  text-align:center;
  justify-content: center;
  font-weight:bold;
  color:#e4eff2;
}
</style>