<template>
  <div class="bottom-navigation">
    <v-bottom-navigation :color="color" fixed shift app>
      <v-btn
        v-for="item in items"
        :key="item.title"
        @click="scrollTo(item.path)"
        class="text-decoration-none"
      >
        <span>{{ item.title }}</span>

        <v-icon>{{ item.icon }}</v-icon>
      </v-btn>
    </v-bottom-navigation>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data: () => ({
    enableBlog: false,
    items: [
      { title: "Home", path: "#aboutme-section", icon: "mdi-home" },
      { title: "Skills", path: "#skills-section", icon: "mdi-star" },
      { title: "Projects", path: "#projects-section", icon: "mdi-code-braces-box" },
      { title: "Experience", path: "#timeline-section", icon: "mdi-timeline" },
    ],
  }),
  computed: {
    color() {
      switch (this.value) {
        case 0:
          return "#fff";
        case 1:
          return "#fff";
        case 2:
          return "#fff";
        case 3:
          return "#fff";
        default:
          return "#fff";
      }
    },
  },
  methods: {
    scrollTo(section) {
       try {
        this.$vuetify.goTo(section);
      } catch {
        this.$router.push("/"+section);
      }
    },
  },
  async created() {
    try {
      if(this.enableBlog == true){
        const response = await axios.get(
          `${process.env.VUE_APP_SERVICE_URL}/blogs/count`
        );
        if(response.data > 0){
        this.items.push(
          {
            title: "Blogs",
            path: "#blogs-section",
            icon: "mdi-card-text",
          }
        )
        }
      }
    } catch (error) {
      this.error = error;
    }
  },
};
</script>

<style lang="scss" scoped>
@media only screen and (min-width: 1265px) {
  .v-bottom-navigation {
    display: none !important;
  }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .v-item-group.v-bottom-navigation .v-btn {
    min-width: 60px;
    font-size: 0.65rem;
  }
}
</style>