<template>
  <section id="viewblog-section">
    <v-skeleton-loader
      v-if="loading"
      :loading="loading"
      v-bind="attrs"
      type="image, article"
    ></v-skeleton-loader>
    <v-container id="wrapper-container" v-if="blog">
      <v-carousel
        v-if="blog.images"
        hide-delimiters
        :show-arrows="blog.images.length > 1"
        height="600"
      >
        <v-carousel-item
          v-for="(image, i) in blog.images"
          :key="i"
          :src="getFullURL(image.url)"
          reverse-transition="fade-transition"
          transition="fade-transition"
        ></v-carousel-item>
      </v-carousel>
      <v-container>
        <h2>{{ blog.title }}</h2>
        <v-spacer></v-spacer>
        <strong>{{ blog.author.firstname }} {{ blog.author.lastname }}</strong> . Last updated : {{ formatedUpdatedAt }}
        <v-spacer></v-spacer>
        <div v-html="markdownToHtml"></div>
      </v-container>
      <v-container>
        <v-chip small color="secondary" class="white--text">
          {{ blog.category.name }}
        </v-chip>
        <div class="text-center">
          <v-btn
            icon
            class="light-blue--text"
            :href="`https://twitter.com/intent/tweet?text=http://www.lucaslin.me/blogs/${blog.id}`"
            target="_blank"
          >
            <v-icon small>mdi-twitter</v-icon>
          </v-btn>
          <v-btn
            icon
            class="blue--text text--darken-4"
            :href="`https://www.facebook.com/sharer/sharer.php?u=http://www.lucaslin.me/blogs/${blog.id}`"
            target="_blank"
          >
            <v-icon small>mdi-facebook</v-icon>
          </v-btn>
          <v-btn
            icon
            class="blue--text text--darken-4"
            :href="`https://www.linkedin.com/sharing/share-offsite/?url=http://www.lucaslin.me/blogs/${blog.id}`"
            target="_blank"
          >
            <v-icon small>mdi-linkedin</v-icon>
          </v-btn>
        </div>
      </v-container>
    </v-container>
  </section>
</template>

<script>
import axios from "axios";
import { marked } from "marked";
import moment from 'moment';
export default {
  name: "viewblog",
  data() {
    return {
      blog: null,
      attrs: {
        class: "mb-6",
        boilerplate: true,
        elevation: 2,
      },
      loading: true,
    };
  },
  computed: {
    markdownToHtml(){
      return marked(this.blog.content);
    },
    formatedUpdatedAt(){
      return moment(String(this.blog.updated_at)).format('DD MMM YYYY hh:mm a');
    }
  },
  methods: {
    getFullURL(url) {
      return `${process.env.VUE_APP_SERVICE_URL}${url}`;
    },
  },
  async created() {
    try {
      this.loading = true;
      const response = await axios.get(
        `${process.env.VUE_APP_SERVICE_URL}/blogs/${this.$route.params.blog_id}`
      );
      this.blog = response.data;
    } catch (error) {
      this.error = error;
    }
  },
  mounted(){
    const readyHandler = () => {
      if (document.readyState == "complete") {
        this.loading = false;
        document.removeEventListener("readystatechange", readyHandler);
      }
    };
    document.addEventListener("readystatechange", readyHandler);
    readyHandler();
  }
};
</script>