<template>
  <section id="blogs-section" v-if="blogs.length>0">
    <h3 class="text-center">{{ title }}</h3>
    <v-container fluid>
      <v-row>
        <v-col cols="12" md="4" sm="12" v-for="blog in blogs" :key="blog.id">
          <v-card class="mx-auto">
            <v-carousel
              v-if="blog.images"
              hide-delimiters
              :show-arrows="blog.images.length > 1"
              height="300"
            >
              <v-carousel-item
                v-for="(image, i) in blog.images"
                :key="i"
                :src="getFullURL(image.url)"
                reverse-transition="fade-transition"
                transition="fade-transition"
              ></v-carousel-item>
            </v-carousel>

            <v-card-title>
              <router-link
                class="text-decoration-none"
                v-bind:to="{ name: 'View-Blog', params: { blog_id: blog.id } }"
              >
                {{ blog.title }}
              </router-link>
            </v-card-title>

            <v-card-subtitle>
              <!-- <v-avatar size="30px">
                <img alt="Avatar"  />
              </v-avatar> -->
              {{ blog.author.firstname }}
              {{ blog.author.lastname }}</v-card-subtitle
            >

            <v-card-text>
              <v-chip small color="secondary">
                {{ blog.category.name }}
              </v-chip>
            </v-card-text>
            <v-card-text class="text-truncate">
              {{ blog.content }}
            </v-card-text>

            <v-card-actions>
              <v-btn
                icon
                class="light-blue--text"
                :href="`https://twitter.com/intent/tweet?text=http://www.lucaslin.me/blogs/${blog.id}`"
                target="_blank"
              >
                <v-icon small>mdi-twitter</v-icon>
              </v-btn>
              <v-btn
                icon
                class="blue--text text--darken-4"
                :href="`https://www.facebook.com/sharer/sharer.php?u=http://www.lucaslin.me/blogs/${blog.id}`"
                target="_blank"
              >
                <v-icon small>mdi-facebook</v-icon>
              </v-btn>
              <v-btn
                icon
                class="blue--text text--darken-4"
                :href="`https://www.linkedin.com/sharing/share-offsite/?url=http://www.lucaslin.me/blogs/${blog.id}`"
                target="_blank"
              >
                <v-icon small>mdi-linkedin</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "Blogs",
  data: () => ({
    show: false,
    blogs: [],
    enableBlog: false,
  }),
  props: ["title"],
  methods:{
    getFullURL(url){
      return `${process.env.VUE_APP_SERVICE_URL}${url}`;
    }
  },
  async created() {
    try {
      if(this.enableBlog == true){
        const response = await axios.get(
          `${process.env.VUE_APP_SERVICE_URL}/blogs`
        );
        this.blogs = response.data;
      }
    } catch (error) {
      this.error = error;
    }
  },
};
</script>